@import url(https://fonts.googleapis.com/css2?family=Karla:wght@500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fira+Code&display=swap);
body {
    font-family: "Karla", sans-serif !important;
    font-weight: 500;
    margin: 0;
    background-color: #f6f7f9 !important;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu",
        "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.banner-img {
    width: calc(300px + 15vw);
}

.poweredby-text {
    font-size: 1.2em;
}

.poweredby-img {
    width: calc(80px + 1.2vw);
}

.github-img {
    width: 32px;
    margin-left: 2px;
}

.textfield-container {
    background-color: #fff !important;
    height: 60vh;
}

.textfield-container-dark {
    background-color: #222 !important;
    height: 60vh;
}

.textfield-input:active,
.textfield-input:hover,
.textfield-input:focus,
.textfield-input {
    font-size: 1.1em !important;
    height: 100% !important;
    border: none !important;
    box-shadow: none !important;
    outline: none !important;
    resize: none;
}

.textfield-input-dark:active,
.textfield-input-dark:hover,
.textfield-input-dark:focus,
.textfield-input-dark {
    color: #eee !important;
    background-color: #222 !important;
    font-family: "Fira Code", monospace;
    height: 100% !important;
    border: none !important;
    box-shadow: none !important;
    outline: none !important;
    resize: none;
}

.card-footer {
    border: none !important;
    background-color: #00000000 !important;
}

.code-toggle {
    position: absolute;
    top: 15px;
    right: 15px;
}

@media (max-width: 768px) {
    .code-toggle {
        top: auto;
        bottom: 75px;
    }
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

